*{
    margin: 0;
    padding: 0;
}

body{
    background-image: url(./IMG/bg-desktop.svg);
    background-color: hsl(257, 40%, 49%);
    margin-left: 70px;

    header{
        width: 100%;
        height: 182px;
        border: 3px solid red;

        img{
            border: 2px solid green;
            width: 205px;
            height: 53px;
            margin-top: 30px;
            margin-bottom: 70px;
        }
    }

    main{
        border: 3px solid red;
        width: 100%;
        height: 500px;

        display: flex;
        flex-direction: row;

        #layoutPhoto{
            img{
                width: 721px;
                height:498px;
            }
        }

        #text{
            h2{
                color: white;
            }
        }
    }

    footer{
        border: 3px solid red;
    }
}